var OhmGrammarTextEditor = require("./components/grammarEnabledTextEditor/ohmGrammarTextEditor.js").OhmGrammarTextEditor;

var language = require("./language.js"),
    toAST = require("./toAST.js"),
    grammarSlice = require("./grammarSlice.js").grammarSlice;

var keywordTags = language.grammar.ruleBodies['keyword'].terms.map(term=> term.ruleName);

let grammar = language.grammar,
    semantics = language.semantics;

let exampleNode = document.querySelector('pre#example');
let example = exampleNode.textContent;
exampleNode.textContent = "";
let match;
try {
  match = grammar.match(example);
} catch (e) {
  console.error(e, match.message);
}

let ohmGrammarTextEditor = new OhmGrammarTextEditor(
  document.querySelector('exampleText'),{
    body: example, grammar
  });



